<script>
// @ is an alias to /src

export default {
  name: 'Home',
  components: {
  },
  data() {
    return {
      jatekid: -1,
      nev: "",
      szobamod: 0,
      csatljatekazonosito: '',
      jatekosok: [],
      indithato: false,
      betu: '',
      jatek: false,
      tablazat: {
        adatok:[/*{betu: "A", orszag:"Albánia", varos: "Albertirsa", fiu: "Absalom", lany: "Anna", allat:"Anakonda", noveny: "Alma", targy: "Almahámozó"}*/],
        uj: {orszag:"", varos: "", fiu: "", lany: "", allat:"", noveny: "", targy: ""}
      },
      tobbijatekosmodal: {
        modal: false,
        tablazat: {
          adatok: []
        }
      }
    }
  },
  sockets: {
    jatekid(id) {
      this.jatekid=id;
    },
    jatekosvaltozas(valtozas) {
      if(valtozas.irany==1) this.jatekosok.push({id: valtozas.jatekos.id, nev: valtozas.jatekos.nev})
      else if(valtozas.irany==-1)  this.jatekosok.splice(this.jatekosok.findIndex(j => j.id==valtozas.jatekos),1)
      console.log(this.jatekosok)
    },
    jatekosok(jatekosok) {
      this.jatekosok=jatekosok;
    },
    start(betu) {
      this.betu=betu
      this.indithato=false;
      this.jatek=true;
    },
    stop() {
      this.$socket.emit("vegallas",{...this.tablazat.uj, jatekid: this.jatekid});
      this.tobbijatekosmodal.modal=true
      this.jatek=false;
    },
    jatekosszavai(jatekos) {
      this.tobbijatekosmodal.tablazat.adatok.push(jatekos)
    }
  },
  methods: {
    elindithato() {
      this.tablazat.uj= {orszag:"", varos: "", fiu: "", lany: "", allat:"", noveny: "", targy: ""}
      this.$socket.emit("indithato",this.jatekid)
      this.indithato=true;
    },
    ujjatek() {
      this.$socket.emit("ujjatek",this.nev)
    },
    csatlakozasjatekhoz(){
      this.$socket.emit("csatlakozasjatekhoz",{nev:this.nev, id: this.csatljatekazonosito})
    },
    stop() {
      this.$socket.emit("stop",this.jatekid);
    }
  }
}
</script>
